@import url("https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@200;400&family=Barlow:ital,wght@0,600;1,500&family=Bellefair&family=Big+Shoulders+Display:wght@700&family=Lexend+Deca&family=Outfit:wght@700&family=Overpass:wght@300;600&family=PT+Sans&family=Public+Sans:wght@300;400;700&family=Roboto&family=Ubuntu:wght@400;500;700&display=swap");

* {
  margin: 0;
}

body {
  margin: 0;
  font-family: "Public Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  color: hsl(233, 26%, 24%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media screen and (max-width: 375px) {
  .App {
    overflow-x: hidden;
  }
}
