.choose-section {
    margin-top: -10px;
    padding-inline: 150px;
    padding-block: 100px;
    background-color: hsl(220, 16%, 96%);
}

.choose-section > h2 {
    font-weight: 400;
    font-size: 30px;
    margin-bottom: 20px;
}

.choose-section > p {
    width: 600px;
    color: hsl(233, 8%, 62%);
    margin-bottom: 60px;
}

.choose {
    width: 280px;
}

.choose > h2 {
    font-weight: 400;
    font-size: 22px;
    margin-block: 30px;
}

.choose > p {
    color: hsl(233, 8%, 62%);
    line-height: 1.5em;
}

.choose-container {
    display: flex;
    gap: 40px;
}

@media screen and (max-width: 375px) {
    .choose-section {
        margin-top: -60px;
        display: flex;
        flex-direction: column;
        padding-inline: 30px;
    }

    .choose-section > h2 {
        text-align: center;
    }

    .choose-section > p {
        width: 100%;
        text-align: center;
    }

    .choose {
        text-align: center;
        width: 100%;
    }

    .choose-container {
        flex-direction: column;
    }
}