.footer {
    padding-inline: 150px;
    padding-block: 50px;
    background-color: hsl(233, 26%, 24%);
    display: flex;
    gap: 120px;
    width: 100%;
}

.container-1 {
    width: 150px;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.icons-container {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
}

.container-2 {
    color: white;
    font-size: 15px;
    width: 150px;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
}

.container-3 {
    color: white;
    font-size: 15px;
    width: 150px;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
}

.container-4 {
    margin-left: 250px;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.container-4 > p {
    color: hsl(233, 8%, 62%);
}

@media screen and (max-width: 375px) {
    .footer {
        padding-inline: 30px;
        align-items: center;
        flex-direction: column;
        gap: 30px;
        width: 300px;
    }

    .container-1 {
        height: 60px;
        align-items: center;
    }

    .container-2 {
        align-items: center;
    }

    .container-3 {
        align-items: center;
    }

    .container-4 {
        margin-left: 0;
        align-items: center;
    }
}