.hero {
    width: 100%;
    height: 100vh;
    background-image: url(../../assets/bg-intro-desktop.svg);
    background-repeat: no-repeat;
    /* background-size: contain; */
    background-position-x: 275%;
    background-position-y: 80%;
}

.mobile-illustration {
    position: relative;
    left: 58%;
    bottom: 17%;
    z-index: 0;
}

.header-content {
    width: 450px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    position: relative;
    left: 150px;
    bottom: 110%;
}

.header-content > h1 {
    font-weight: 400;
    font-size: 50px;
}

.header-content > p {
    color: hsl(233, 8%, 62%);
    font-size: 18px;
    font-weight: 300;
    line-height: 1.5em;
}


@media screen and (max-width: 375px) {
    .hero {
        background-image: url(../../assets//bg-intro-mobile.svg);
        background-position-x: 10%;
        background-position-y: -20%;
    }

    .mobile-illustration {
        scale: 0.45;
        left: -56%;
        bottom: 52%;
    }

    .header-content {
        align-items: center;
        left: 20px;
        bottom: 85%;
        width: 90%;
        gap: 25px;
    }

    .header-content > h1 {
        font-size: 40px;
        text-align: center;
    }

    .header-content > p {
        text-align: center;
        font-size: 15px;
        font-weight: 400;
    }
}