.articles-section {
  padding-inline: 150px;
  padding-block: 100px;
}

.articles-section > h2 {
  font-weight: 400;
  font-size: 30px;
  margin-bottom: 20px;
}

.articles > p {
  color: hsl(233, 8%, 62%);
  margin-left: 20px;
  width: 240px;
}

.name {
  font-size: 12px;
  margin-top: 20px;
}

.description {
  font-size: 16px;
}

.articles > h2 {
  margin-block: 5px;
  margin-left: 20px;
  width: 220px;
  font-weight: 400;
  font-size: 18px;
}

.articles-container {
  display: flex;
  gap: 20px;
}

.articles {
  flex: 1;
}

.articles > img {
  width: 280px;
  height: 200px;
}


@media screen and (max-width: 375px) {
    .articles-section {
        padding-inline: 30px;
    }

    .articles-container {
        flex-direction: column;
        gap: 60px;
    }

    .articles {
        width: 100%;
    }

    .articles > img {
        width: 100%;
    }

    .articles > p {
        width: 90%;
    }
}