.navigation {
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-inline: 150px;
    background-color: white;
    position: relative;
    z-index: 5;
}

.logo {
    object-fit: contain;
}

.navbar > ul {
    list-style: none;
    display: flex;
    gap: 30px;
}

.navbar > ul > li {
    color: hsl(233, 8%, 62%);
    font-size: 14px;
    cursor: pointer;
}

@media screen and (max-width: 375px) {
    .navigation {
        padding-inline: 20px;
    }
}