.dialog {
    border: none;
    position: fixed;
    left: 5%;
    top: 15%;
    width: 80%;
    border-radius: 15px;
}

.dialog > ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
    padding-block: 20px;
    width: 80%;
    color: hsl(233, 8%, 62%);
}

.dialog::backdrop {
    position: relative;
    top: 12%;
}